<script>
export default {
	data(){
		return{
			dynList:[],
			noticeList:[],
			identity:''
		}
	},
	created(){	
		var _self = this;
		var url="http://api.7171.la/api/article";            
		  this.$http.post(url,{token:'ahxdgz',code:'93b43f5bbc45faaad4c02ebce991b2de',type:'46,47'}
			).then(function (data) {
       		var json = data.data;
		  	_self.totalList = json;
			for (var i = 0;i<_self.totalList.length;i++) {
				if(_self.totalList[i].type == "46"){
					if(_self.dynList.length<5){
						_self.dynList.push(_self.totalList[i]);
					}
				}
				if(_self.totalList[i].type == "47"){
					if(_self.noticeList.length<5){
						_self.noticeList.push(_self.totalList[i]);
					}
				}
			}
    	});
	}, 
	activated(){
	    	document.documentElement.scrollTop = 0
	    },
	methods: {
	    select(uri){
	    	this.$router.push({ path: uri });
	    },
	   
	    validate(str){
	       	var reg= /(^\d{15}$)|(^\d{18}$)|(^\d{17}(\d|X|x)$)/; 
			if(reg.test(str) === false) { 
				document.getElementById('identity').style.display = 'block';
				document.getElementById('btn').setAttribute('disabled','disabled');
	       	}else{
	       		document.getElementById('identity').style.display = 'none';
	       		$("#btn").removeAttr("disabled");
	       	}
    	},
    	submitForm(event) {
            event.preventDefault();
            validate(this.identity);
            
            let config = {
              headers: {
                'Content-Type': 'multipart/form-data'
              }
            }
 
      /*      this.$http.post('/upload', formData, config).then(function (res) {
              if (res.status === 2000) {
	//	                这里做处理
              }
            })*/
        }
	},
	mounted(){
	}
	
}	
</script>

