<template>
	<div>
		<div class="container center banner">
			<img src="../../static/img/banner2.jpg" class="img-responsive pc-bg" />
			<img src="../../static/img/m-banner2.png" class="img-responsive m-bg" />
		</div>
		<div class="container center info-content">
			<div class="col-lg-3 col-md-3 col-sm-12 col-xs-12">
				<!--pc-->
				<div class="pc-item-nav">
					<div class="zhaosheng">
						<router-link to="/sign_online">
							<img src="../../static/img/bg11.png" class="img-responsive" />
						</router-link>
						<router-link to="/special_major_accouting">
							<img src="../../static/img/bg12.png" class="img-responsive" />
						</router-link>		
					</div>
				</div>
			</div>
			<div class="col-lg-9 col-md-9 col-sm-12 col-xs-12 info">
				<div class="info-title">
					<h4>学院简介</h4>
					<div class="info-title-right pc-bg">
						<span><router-link to="/">首页</router-link></span> 
						<span>&gt;</span> 
						<span><router-link to="/sc_info">学院简介</router-link></span>
					</div>
				</div>
				<div class="col-lg-12 col-md-12 col-sm-12 col-xs-12">
					<span v-html="infoList.content" class="sc-info" style="text-indent: 32px;"></span>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
export default {
	data(){
        return{
        	infoList:[]
        }
    },
    created:function(){	
    	var _self = this;
    	var url1="http://api.7171.la/api/synopsis";            
		  this.$http.post(url1,{token:'ahxdgz',code:'93b43f5bbc45faaad4c02ebce991b2de'}
			).then(function (data) {
        	var json = data.data;  
		  	_self.infoList = json;
    	});   
    },
	activated(){
		 document.documentElement.scrollTop = 0
	},
	beforeRouteEnter (to, from, next) {
		next(vm => {
		    document.title="学校简介_安徽现代信息工程职业学院——互联网学院招生网";
	  	});
	}
}
</script>

<style>
	.sc-info img{
		display: block;
		margin: 15px auto;
		max-width: 100%;
	}
</style>


