import Vue from 'vue'
import Router from 'vue-router'
import Index from '@/components/index'
import Sc_info from '@/components/sc_info'
import Register_guide from '@/components/register_guide'
import Admission_policy from '@/components/admission_policy'
import School_admission_brochure from '@/components/school_admission_brochure'
import Contact from '@/components/contact'
import Enrollment_plan from '@/components/enrollment_plan'
import News_page1 from '@/components/news_page1'
import News_page2 from '@/components/news_page2'
import News_page3 from '@/components/news_page3'
import News_page4 from '@/components/news_page4'
import Major1 from '@/components/special_major_accouting'
import Major2 from '@/components/special_major_accouting1'
import Major3 from '@/components/special_major_artdesign'
import Major4 from '@/components/special_major_artdesign1'
import Major5 from '@/components/special_major_artdesign2'
import Major6 from '@/components/special_major_computer'
import Major7 from '@/components/special_major_computer1'
import Sign_online from '@/components/sign_online'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'HelloWorld',
      component: Index
    },
    {
    	path: '/sc_info',
      component: Sc_info
    },
    {
    	path: '/register_guide',
      component: Register_guide
    },
    {
    	path: '/admission_policy',
      component: Admission_policy
    },
    {
    	path: '/school_admission_brochure',
      component: School_admission_brochure
    },
    {
    	path: '/contact',
      component: Contact
    },
    {
    	path: '/enrollment_plan',
      component: Enrollment_plan
    },
    {
      path: '/news_page1/:id',
      name: 'news',
      component: News_page1
    },
    {
      path: '/news_page2/:id',
      name: 'news2',
      component: News_page2
    },
		{
      path: '/news_page3/:id',
      name: 'news3',
      component: News_page3
    },
    {
      path: '/news_page4/:id',
      name: 'new4',
      component: News_page4
    },
    {
      path: '/special_major_accouting',
      component: Major1
    },
    {
      path: '/special_major_accouting1',
      component: Major2
    },
    {
      path: '/special_major_artdesign',
      component: Major3
    },
    {
      path: '/special_major_artdesign1',
      component: Major4
    },
    {
      path: '/special_major_artdesign2',
      component: Major5
    },
    {
      path: '/special_major_computer',
      component: Major6
    },
    {
      path: '/special_major_computer1',
      component: Major7
    },
    {
      path: '/sign_online',
      component: Sign_online
    },
  ]
})



