<template>
	<div>
		<div class="container center banner">
			<img src="../../static/img/banner2.jpg" class="img-responsive pc-bg" />
			<img src="../../static/img/m-banner2.png" class="img-responsive m-bg" />
		</div>
		
		<div class="container center">
			<div class="info sign">
				<div class="info-title">
					<h3>报名信息填写</h3>
				</div>
				<div class="col-lg-12 col-md-12 col-sm-12 col-xs-12 content ">
					<form class="form-horizontal sign-form">
						<div class="form-group">
							<div class="col-md-2 col-xs-12">
							    <label class="control-label" for="exampleInputName2"><i>*</i>姓名：</label>
							</div>
							<div class="col-md-5 col-xs-12">
							    <input type="text" class="form-control" v-model="name" @blur="name1(name)">
							</div>
							<div class="col-md-5 col-xs-12">
								
							</div>
						</div>
						<div class="form-group">
							<div class="col-md-2 col-xs-12">
						    	<label class="control-label" for="exampleInputName2"><i>*</i>性别：</label>
							</div>
							<div class="col-md-5 col-xs-12">
							    <div class="choose">
								   <label class="radio">男<input type="radio" value="1" name="sex" v-model="sex" @change="sexChoice(sex)"><i></i></label>
								   <label class="radio">女<input type="radio" value="2" name="sex" v-model="sex" @change="sexChoice(sex)"><i></i></label>
								</div>
							</div>
							<div class="col-md-5 col-xs-12">
								<span id="sex"></span>
							</div>
						</div>
						<div class="form-group">
							<div class="col-md-2 col-xs-12">
						    	<label class="control-label" for=""><i>*</i>联系电话：</label>
							</div>
							<div class="col-md-5 col-xs-12">
							    <input type="text" class="form-control" v-model="tele" placeholder="手机号码、固定号码均可" @blur="telephone(tele)" >
							</div>
							<div class="col-md-5 col-xs-12">
								<span id="telephone">（固定电话请加区号,如“0554-1234567”）</span>
							</div>
						</div>
						<div class="form-group">
							<div class="col-md-2 col-xs-12">
						    	<label class="control-label" for=""><i>*</i>毕业学校：</label>
							</div>
							<div class="col-md-5 col-xs-12">
							    <input type="text" class="form-control" v-model="sch" placeholder="请输入毕业学校" @blur="school(sch)">
							</div>
							<div class="col-md-5 col-xs-12">
								<span id="school">（毕业学校名称）</span>
							</div>
						</div>
						<div class="form-group">
							<div class="col-md-2 col-xs-12">
						    	<label class="control-label" for=""><i>*</i>文化程度：</label>
							</div>
							<div class="col-md-5 col-xs-12">
							    <div class="choose">
								   <label class="radio">初中<input type="radio" value="1" name="record" v-model="record" @blur="culture(record)"><i></i></label>
								   <label class="radio">高中<input type="radio" value="2" name="record" v-model="record" @blur="culture(record)"><i></i></label>
								   <label class="radio">技校<input type="radio" value="3" name="record" v-model="record" @blur="culture(record)"><i></i></label>
								</div>
							</div>
							<div class="col-md-5 col-xs-12">
								<span id="record"></span>
							</div>
						</div>
						<div class="form-group">
							<div class="col-md-2 col-xs-12">
						    	<label class="control-label" for="exampleInputName2">QQ号码：</label>
							</div>
							<div class="col-md-5 col-xs-12">
							    <input type="text" class="form-control" id="exampleInputName2" placeholder="" v-model="qq">
							</div>
							<div class="col-md-5 col-xs-12">
								<span>（留下QQ，方便咨询）</span>
							</div>
						</div>
						<div class="form-group">
							<div class="col-md-2 col-xs-12">
						    	<label class="control-label" for="exampleInputName2">微信号：</label>
							</div>
							<div class="col-md-5 col-xs-12">
							    <input type="text" class="form-control" id="exampleInputName2" placeholder="" v-model="wechat">
							</div>
							<div class="col-md-5 col-xs-12">
								<span>（随时联系更便捷）</span>
							</div>
						</div>
						<div class="form-group">
							<div class="col-md-2 col-xs-12">
						    	<label class="control-label" for="exampleInputName2">家庭住址：</label>
							</div>
							<div class="col-md-5 col-xs-12">
							    <input type="text" class="form-control" v-model="add" placeholder="请输入家庭住址"  >
							</div>
							<div class="col-md-5 col-xs-12">
								<span id="address">（请填写现住的详细地址，方便邮寄录取通知书）</span>
							</div>
						</div>
						<div class="form-group">
					    	<div class="col-xs-12 col-md-2">
					    		<label class="control-label"><i>*</i>验证码：</label>
					    	</div>
						    <div class="col-md-5 col-xs-12 yanzheng">
								<input type="text" class="form-control" v-model="code" placeholder="请输入验证码" @blur="yanzheng(code)" style="width: 50%;float: left;"/>
						        <div id="v_container"></div>
						    </div>
						    <span class="col-md-5 col-xs-12">看不清  点击图片换一张</span>
					    </div>
						<div class="form-group">
							<div class="col-md-2 col-xs-12">
						   
							</div>
							<div class="col-md-7 col-xs-12 tip">
							    <span>注：带<i>*</i>号为必填项，填写完毕后请按“提交”按钮</span>
							</div>
							<div class="col-md-3 col-xs-12">
								
							</div>
						</div>
						
						<div class="form-group">
							<div class="col-md-2 col-xs-12">
						   
							</div>
							<div class="col-md-5 col-xs-12 tip">
							    <button type="button" class="btn" id="btn" @click="submitForm($event)">提交</button>
							</div>
							<div class="col-md-5 col-xs-12">
								
							</div>
						</div>
					</form>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
	import '../../static/js/gVerify.js'
	var verifyCode;
	export default {
		data(){
	        return{
				name:'',
				sex:'',
				tele:'',
				sch:'',
				add:'',
				record:'',
				code:'',
				//选填
				qq:'',
				wechat:'',
	        }
	    },
	    created:function(){	
	    	
	    },
	    methods:{
	    	name1(str){
	    		if(str.length == 0){
	    			document.getElementById('name').innerHTML = "请输入姓名！！";
	    			document.getElementById('btn').setAttribute('disabled','disabled');
	    			return 'false';
	    		}else{
	    			document.getElementById('name').innerHTML = "";
		       		document.getElementById('btn').disabled=false; 
	    		}
	    	},
	    	yanzheng(c){
				var res = verifyCode.validate(c);
	            if(res){
	                document.getElementById('btn').disabled=false; 
	                return res;
	            }else{
	                alert("验证码错误");
	                verifyCode.refresh();
	                document.getElementById('btn').setAttribute('disabled','disabled');
	                return 'false';
	            }
			},
			school(str){
				if(str.length == 0){
	    			document.getElementById('school').innerHTML = "请输入毕业学校！！";
	    			document.getElementById('btn').setAttribute('disabled','disabled');
	    			return 'false';
	    		}else{
	    			document.getElementById('school').innerHTML = "";
		       		document.getElementById('btn').disabled=false; 
	    		}
			},
	    	telephone(str){
		       	var reg = /^1\d{10}$/;
		       	var reg1 = /^\d{3,4}-\d{7,8}$/;
		       	if(str.length == 0){
	    			document.getElementById('telephone').innerHTML = "请输入联系号码！！";
	    			document.getElementById('btn').setAttribute('disabled','disabled');
	    			return 'false';
	    		}
				if(reg.test(str) === false && reg1.test(str)===false) { 
					document.getElementById('telephone').innerHTML = "请输入合法联系号码！！";
					document.getElementById('btn').setAttribute('disabled','disabled');
					return 'false';
		       	}else{
		       		document.getElementById('telephone').innerHTML = "";
		       		document.getElementById('btn').disabled=false; 
		       	}
	    	},
	    	culture(str){
	    		this.record	 = str;
	    		if(str.length == 0){
	    			document.getElementById('record').innerHTML = "请选择文化程度！！";
	    			document.getElementById('btn').setAttribute('disabled','disabled');
	    			return 'false';
	    		}else{
		       		document.getElementById('record').innerHTML = "";
		       		document.getElementById('btn').disabled=false; 
		       	}
	    	},
	    	sexChoice(str){
	    		this.sex = str;
	    		if(str.length == 0){
	    			document.getElementById('sex').innerHTML = "请选择性别！！";
	    			document.getElementById('btn').setAttribute('disabled','disabled');
	    			return 'false';
	    		}else{
		       		document.getElementById('sex').innerHTML = "";
		       		document.getElementById('btn').disabled=false; 
		       	}
	    	},
	    	removeAllSpace(str) {
	    		var str = str.replace(/\s+/g, "");
		     	return str;
		    },
	    	submitForm(event){
	    		var result = this.yanzheng(this.code);
	    		var result1 = this.sexChoice(this.sex);
	    		var result2 = this.culture(this.record);
	    		this.record = parseInt(this.record);
	    		var result3 = this.school(this.sch);
	    		var result4 = this.telephone(this.tele);
	    		var result5 = this.name1(this.name);
	    		if(result == 'false' || result1 == 'false' || result2 == 'false'|| result3 == 'false'|| result4 == 'false'|| result5 == 'false')
            		return;
	    		this.sch = this.removeAllSpace(this.sch );
	    		this.qq = this.removeAllSpace(this.qq );
	    		this.address = this.removeAllSpace(this.add );
	    		this.wechatnum = this.removeAllSpace(this.wechat );
	    		var data = {};
	    		data['name'] = this.name;
	    		data['sex'] = parseInt(this.sex);
	    		data['finishschool'] = this.sch;
	    		data['phone'] = this.tele;
	    		data['qq'] = this.qq;
	    		data['wechatnum'] = this.wechat;
	    		data['address'] = this.add;
	    		data['culture'] = this.record;
				this.$http.post('http://api.7171.la/api/addreginfo',{token:'ahxdgz',code:'93b43f5bbc45faaad4c02ebce991b2de',data:data}
					).then(function (res) {
		        	if(res.data.status == 1){
		        		alert('报名成功！');
		        	}else if(res.data.status == -1){
		        		alert('访问失败！');
		        	}else if(res.data.status == -2){
		        		alert('报名失败！');
		        	}else if(res.data.status == -3){
		        		alert('该身份证号码已报名！');
		        	}else if(res.data.status == -4){
		        		alert('该手机号码已报名！');
		        	}
		    	});  
	    	}
	    },
		activated(){
			 document.documentElement.scrollTop = 0
		},
		beforeRouteEnter (to, from, next) {
			next(vm => {
			    document.title="在线报名_安徽现代信息工程职业学院——互联网学院招生网";
		  	});
		},
		mounted(){
			verifyCode = new GVerify("v_container");
		}
	}
</script>

<style>
	.tip i{
		margin: 0 !important;
	}
	#v_container{
		width: 45%;
		float: left;
		margin-left: 1%;
		/*padding: 5px 0;*/
	}
</style>


